<template>
  <at-modal v-model="dialog" width="680">
    <div slot="header" style="text-align: center">
      <span>Загрузка модели</span>
    </div>
    <div class="row at-row no-gutter">
      <div class="col-16 models-list">
        <vue-custom-scrollbar class="scroll-area" :settings="settings">
        <ul class="loaded-list">
          <li
            v-for="(list, i) of lists[0].models"
            :key="`list_${i}`"
            @click="getModel(list)"
          >
            <i class="icon icon-file-text"></i>
            <span>{{ list.label }}</span>
          </li>
          <li
            v-for="(list, i) of lists[1].models"
            :key="`list_${i}`"
            @click="getModel(list)"
          >
            <i class="icon icon-file-text"></i>
            <span>{{ list.label }}</span>
            <div class="remove"></div>
          </li>
        </ul>
        </vue-custom-scrollbar>
      </div>
      <div class="col-8">
        <div class="model-arch">
          <div class="wrapper hidden">
            <div class="modal-arch-info">
              <div class="model-arch-info-param name">
                Name: <span>cifar10_conv2d</span>
              </div>
              <div class="model-arch-info-param input_shape">
                Input shape: <span>[32,32,3]</span>
              </div>
              <div class="model-arch-info-param datatype">
                Datatype: <span>2D</span>
              </div>
            </div>
            <div class="model-arch-img">
              <img
                alt=""
                width="100"
                height="200"
                src="data:image/png;base64,"
              />
            </div>
            <div class="model-save-arch-btn"><button>Загрузить</button></div>
          </div>
        </div>
      </div>
    </div>
    <div slot="footer"></div>
  </at-modal>
</template>

<script>
import vueCustomScrollbar from "vue-custom-scrollbar";
import { mapGetters } from "vuex";
export default {
  name: "ModalWindowLoadModel",
  components: {
    vueCustomScrollbar
  },
  data: () => ({
    lists: [],
    settings: {
      suppressScrollY: false,
      suppressScrollX: true,
      wheelPropagation: false,
    },
  }),
  computed: {
    ...mapGetters({}),
    dialog: {
      set(value) {
        this.$store.dispatch("modeling/setDialog", value);
      },
      get() {
        return this.$store.getters["modeling/getDialog"];
      },
    }
  },
  methods: {
    CloseModalWindow() {
      this.$emit("CloseModalWindow", false);
    },
    async load() {
      const data = await this.$store.dispatch("modeling/loadModel");
      this.lists = data;
    },
    async getModel({ name, is_terra }) {
      const res = {
        method: "get",
        url: "/api/v1/modeling/models/",
        data: {
          model_file: name,
          is_terra,
        },
      };
      const data = await this.$store.dispatch("modeling/axios", res);
      console.log(data);
    },
  },
  watch: {
    dialog: {
      handler(value) {
        console.log(value);
        if (value) {
          this.load();
        }
      },
    },
  },
};
</script>

